$backgroundColor: #7bc9cc;
$bodyColor: #fff;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

html, body {
  height: 100%;
}

a, a:visited, a:hover {
  color: #ffffff;
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  margin: 0;
}
.flex-container {
  height: 90%;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.row {
  width: auto;
  border: 1px solid black;
}
.flex-item {
  background-color: tomato;
  padding: 5px;
  width: 500px;
  height: 500px;
  margin: 10px;
  line-height: 50px;
  color: white;
  font-weight: bold;
  font-size: 2em;
  text-align: center;
}

.footer {
  text-align: center;
}


.internal {
  padding-left: 60px;
  color: #232323;
}